import './quick-entry.css';
import React from 'react';
import { connect } from 'react-redux';
import Draggable from 'react-draggable';
import { showQuickEntry } from './actions';

const QuickEntry = ({ showQuickEntry }) => {
  return (
    <Draggable defaultPosition={{x: 45, y: 164}} cancel=".quick-entry_input" bounds="body">
      <div className="quick-entry">
        <div style={{position:'relative'}}>
          <span className="material-icons quick-entry_icon" aria-hidden="true">add</span>
          <span className="material-icons quick-entry_icon-close" aria-hidden="true" onClick={showQuickEntry()}>close</span>
          <input className="quick-entry_input" defaultValue={null} placeholder="Add Strategy eg: C .FTSE feb 7200" autoFocus/>
        </div>
      </div>
    </Draggable>
  );
};

const mapDispatchToProps = dispatch => ({
  showQuickEntry: () => () => dispatch(showQuickEntry()),
})

export default connect(
  undefined,
  mapDispatchToProps
)(QuickEntry);


