import './search.css';
import React from 'react';
import { connect } from 'react-redux';
import Draggable from 'react-draggable';
import { showSearch } from './actions';

const Search = ({ showSearch }) => {
  return (
    <Draggable defaultPosition={{x: 45, y: 124}} cancel=".search_input" bounds="body">
      <div className="search">
        <div style={{position:'relative'}}>
          <span className="material-icons search_icon" aria-hidden="true">search</span>
          <span className="material-icons search_icon-close" aria-hidden="true" onClick={showSearch()}>close</span>
          <input className="search_input" defaultValue={null} placeholder="Search Workspace" autoFocus/>
        </div>
      </div>
    </Draggable>
  );
};

const mapDispatchToProps = dispatch => ({
  showSearch: () => () => dispatch(showSearch()),
})

export default connect(
  undefined,
  mapDispatchToProps
)(Search);


